import React, { Component } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { addCustomTokens } from '../commons/metamask'
import contractMapMainnet from '@rsksmart/rsk-contract-metadata'
import contractMapTestNet from '@rsksmart/rsk-testnet-contract-metadata'

class TokensComponent extends Component {
  constructor (props) {
    super(props)
    this.isMainnet = process.env.REACT_APP_ENVIRONMENT_ID === '30'
    this.state = { display: false }
    if (this.isMainnet) {
      this.metadataTokens = Object.entries(contractMapMainnet).map(keyValue => { return { address: keyValue[0], ...keyValue[1] } })
    } else { // if testnet
      this.metadataTokens = Object.entries(contractMapTestNet).map(keyValue => { return { address: keyValue[0], ...keyValue[1] } })
    }
    this.metadataTokensToShow = this.metadataTokens.slice(0, 5) // show only first 5

    this.handleAddButtonClick = this.handleAddButtonClick.bind(this)
    this.handleRemoveButtonClick = this.handleRemoveButtonClick.bind(this)
  }

  handleAddButtonClick () {
    this.metadataTokensToShow = this.metadataTokens
    this.setState({
      display: true
    })
  }

  handleRemoveButtonClick () {
    this.metadataTokensToShow = this.metadataTokens.slice(0, 5) // show only first 5
    this.setState({
      display: false
    })
  }

  async addMetadataToken (metadataToken) {
    addCustomTokens(this.isMainnet, metadataToken)
  }

  render () {
    return (
      <Container>
        <Row>
          <Col md={{ span: 1, offset: 1 }} >
            <span className="bullet">4</span>
          </Col>
          <Col >
            <h2 className="step" >Add the RSK tokens! Click below to add the {this.isMainnet ? 'Mainnet' : 'Testnet'} RSK compatible tokens. </h2>
            <Row>
              <Col>
                {
                  this.metadataTokensToShow.map(metadataToken => <button key={metadataToken.symbol} className="buttonAddToken" onClick={() => this.addMetadataToken(metadataToken)} disabled={this.props.step !== 2} >{`Add ${metadataToken.symbol} Token`}</button>)
                }
                <button key='add' onClick={this.handleAddButtonClick} hidden={this.state.display} className="buttonAddToken" >+</button>
                <button key='remove' onClick={this.handleRemoveButtonClick} hidden={!this.state.display} className="buttonAddToken" >-</button>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    )
  }
}

export default TokensComponent
